@import "./fonts.scss";
@import "./variables.scss";

.homepage {
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;

    @media screen and (max-width: $tablet) {
      align-items: flex-start;
      margin-top: 10px;
    }

    h1 {
      font-family: "Trade Gothic Bold Condensed", sans-serif;
      font-size: 180px;
      color: white;
      letter-spacing: -6.71px;
      text-transform: uppercase;
      margin: 0px;
      padding: 0px;

      @media screen and (max-width: $tablet) {
        letter-spacing: -8.63px;
        font-size: 21vw;
        text-align: left;
        margin-left: 16px;
      }
      @media screen and (max-width: 425px) {
        font-size: 18vw;
        letter-spacing: -4.31px;
        line-height: 80px;
      }
    }

    p {
      font-family: "Lato Light", sans-serif;
      font-size: 18px;
      color: white;
      letter-spacing: 0.4px;
      line-height: 24px;
      width: 1116px;
      padding: 0px;
      margin: 0px;
      margin-bottom: 64px;
      margin-top: -30px;
      text-align: center;

      @media screen and (max-width: $homepage-title-break) {
        max-width: none;
        margin-bottom: 40px;
      }

      @media screen and (max-width: 1200px) {
        width: 100%;
        padding: 0px 30px;
        box-sizing: border-box;
      }

      @media screen and (max-width: $tablet) {
        text-align: left;
        font-size: 16px;
        letter-spacing: 0.36px;
        line-height: 24px;
        padding: 0px 16px;
        margin-top: -20px;
      }
    }
  }

  .grid-container {
    padding: 0px 12px;
    box-sizing: border-box;
    .value-grid {
      display: grid;
      width: 100%;
      height: 100%;
      grid-template-columns: repeat(3, minmax(455px, 1fr));
      grid-template-rows: repeat(7, minmax(304px, 1fr));
      row-gap: 20px;

      @media screen and (max-width: $homepage-laptop) {
        grid-template-columns: repeat(2, minmax(352px, 1fr));
        grid-template-rows: repeat(9, minmax(246px, 1fr));

        column-gap: 32px;
        row-gap: 40px;
      }

      @media screen and (max-width: $tablet) {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(12, 1fr);
        row-gap: 40px;
      }

      #Stewardship {
        .title {
          max-width: 55vw;
        }
      }

      .square {
        position: relative;
        pointer-events: none;
        cursor: default;

        & > * {
          pointer-events: auto;
        }

        a {
          text-decoration: none;
        }

        video {
          width: 100%;
        }

        img {
          position: absolute;
          width: 100%;
          top: 0px;
          left: 0;
          opacity: 0%;
          transition: opacity 0.3s ease;
        }
        &:hover {
          img {
            opacity: 1;
          }
        }

        &.s1 {
          grid-column: 1 / span 2;
          grid-row: 1 / span 2;
          margin-right: 16px;
          @media screen and (max-width: $homepage-laptop) {
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-row: 1;
            grid-column: 1;
          }
        }

        &.s2 {
          margin-left: 16px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1;
            grid-row: 3;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 2;
          }
        }
        &.s3 {
          margin-left: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 2;
            grid-row: 3;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 3;
          }
        }

        &.s4 {
          margin-right: 16px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1;
            grid-row: 4;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 4;
          }
        }
        &.s5 {
          grid-column: 2 / span 2;
          grid-row: 3 / span 2;
          margin-left: 16px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 2;
            grid-row: 4;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 5;
          }
        }
        &.s6 {
          margin-right: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1 / span 2;
            grid-row: 5 / span 2;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 6;
          }
        }
        &.s7 {
          margin-right: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1;
            grid-row: 7;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 7;
          }
        }
        &.s8 {
          margin-left: 8px;
          margin-right: 8px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 2;
            grid-row: 7;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 8;
          }
        }
        &.s9 {
          margin-left: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1;
            grid-row: 8;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 9;
          }
        }
        &.s10 {
          grid-column: 1 / span 2;
          grid-row: 6 / span 2;
          margin-right: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 2;
            grid-row: 8;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 10;
          }
        }
        &.s11 {
          margin-left: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 1;
            grid-row: 9;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 11;
          }
        }
        &.s12 {
          margin-left: 16px;
          margin-top: 20px;
          @media screen and (max-width: $homepage-laptop) {
            grid-column: 2;
            grid-row: 9;
            margin: 0px;
          }
          @media screen and (max-width: $tablet) {
            grid-column: 1;
            grid-row: 12;
          }
        }

        &.s2,
        &.s3,
        &.s4,
        &.s6,
        &.s7,
        &.s8,
        &.s9,
        &.s11,
        &.s12 {
          video {
            @media screen and (max-width: $homepage-laptop) {
              width: 100%;
              max-width: none;
            }
          }
        }

        .video-info {
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: flex-start;
          justify-content: space-between;

          p {
            text-decoration: none;
            margin: 0px;
            margin-top: 8px;
          }

          .title,
          .brand {
            font-size: 14px;
            color: white;
            letter-spacing: 0.25px;
            line-height: 20px;

            @media screen and (max-width: $tablet) {
              font-size: 16px;
              letter-spacing: 0.25px;
            }
          }

          .title {
            text-align: left;
            font-family: "Lato Regular", sans-serif;

            @media screen and (max-width: $tablet) {
              max-width: 60vw;
            }
          }

          .brand {
            font-family: "Lato Light", sans-serif;
            text-transform: uppercase;
            text-align: right;
            color: #bebebe;
            font-size: 14px;
            @media screen and (max-width: $tablet) {
              letter-spacing: 1.07px;
              line-height: 16px;
              padding-top: 3px;
            }
          }
        }
      }
    }
  }
}
